html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 20px;
  line-height: 1.5;
  background-color: #cefffc;
  color: #424242;
  max-width: 1280px;
  padding: 20px;
}

.container-input {
display: flex;
justify-content: center;
}

.search-input {
  font-size: 28px;
  line-height: 1.2;
  text-align: left;
  margin: 30px;
  text-align: center;
}

 .card {
   margin-left: 50px;
 }
  .card-title {
     display: flex;
    justify-content: center;
  }

.card-text {
  font-weight: bold;
  font-size: 28px;
  margin: 10px;
}

.card-flag {
  width: 350px;
  height: 335px;
}


.list {
   font-size: 24px;
   font-weight: bold;
   max-width: 500px;
   text-align: left;
   margin: 0 auto;
}
